<template>
  <div>
    <van-form
      id="scroll"
      @submit="onSubmit"
    >
      <div
        v-if="!$route.query.id"
        class="door_type"
      >
        <div class="w_140">收款类型</div>
        <van-radio-group
          v-model="payType"
          direction="horizontal"
          style="display:flex;width:88%;"
        >
          <van-radio
            @click="changePayType(payType)"
            :name='item'
            v-for="(item,i) in payAll"
            :key="i"
          >{{ item }}</van-radio>
        </van-radio-group>
      </div>
      <div
        v-else
        class="scale_box"
      >
        <div class="w28">收款类型</div>
        <div>{{ payAll[dataForm.merchantType - 1] }}</div>
      </div>
      <van-field
        v-if="payType == '业主收款'"
        v-model="user"
        label="选择业主"
        :readonly="!$route.query.id && payType == '业主收款'?false:true"
        @click="!$route.query.id && payType == '业主收款'?user_show = true:''"
      />
      <!-- 门店名称 -->
      <van-field
        style="margin-top:10px;"
        v-model="dataForm.storeName"
        label="门店名称"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        placeholder="请输入门店名称"
        :rules="[{ required: true, message: '请输入门店名称' }]"
      />
      <div
        v-if="!$route.query.id || $route.query.id && $route.query.status == '2'"
        class="door_type"
      >
        <div class="w_140">门店类型</div>
        <van-radio-group
          v-model="doorType"
          direction="horizontal"
          style="display:flex;width:88%;"
        >
          <van-radio
            @click="changeDoorType(doorType)"
            :name='item'
            v-for="(item,i) in organizationTypes"
            :key="i"
          >{{ item }}</van-radio>
        </van-radio-group>
      </div>
      <div
        v-else-if="$route.query.id && $route.query.status != '2'"
        class="scale_box"
      >
        <div class="w28">门店类型</div>
        <div>{{ doorType }}</div>
      </div>
      <!-- 经营者身份证照片 -->
      <div class="shenfen">
        <div class="shen_title">经营者身份证照片</div>
        <div class="shenfen_pic">
          <div>
            <van-uploader
              :max-count="1"
              :disabled="$route.query.id && $route.query.status != '2'?true:false"
              :after-read="upLoadToQiniu_front"
            >
              <div v-if="(fileList.filter(item=>item.title == 'front')).length > 0">
                <img
                  v-for="(item,i) in fileList.filter(item=>item.title == 'front')"
                  :key="i"
                  :src="item.url"
                  style="width:108px;height:70px;"
                >
                <div class="re_push">重新上传</div>
              </div>
              <van-button
                v-else
                icon="add"
                type="primary"
              ></van-button>
            </van-uploader>
            <div class="p10">身份证头像面</div>
          </div>
          <div>
            <van-uploader
              :max-count="1"
              :disabled="$route.query.id && $route.query.status != '2'?true:false"
              :after-read="upLoadToQiniu_back"
            >
              <div v-if="(fileList.filter(item=>item.title == 'back')).length > 0">
                <img
                  v-for="(item,i) in fileList.filter(item=>item.title == 'back')"
                  :key="i"
                  :src="item.url"
                  style="width:108px;height:70px;"
                >
                <div class="re_push">重新上传</div>
              </div>
              <van-button
                v-else
                icon="add"
                type="primary"
              ></van-button>
            </van-uploader>
            <div class="p10">身份证头徽面</div>
          </div>
        </div>
      </div>
      <!-- 姓名、身份证号 -->
      <van-field
        v-if='$route.query.id'
        v-model="dataForm.idCardName"
        label="姓名"
        placeholder="请输入姓名"
        :readonly="$route.query.status == 2?false:true"
        :rules="[{ required: true, message: '请输入姓名' }]"
      />
      <van-field
        v-if='$route.query.id'
        v-model="dataForm.idCardNumber"
        label="身份证号"
        placeholder="请输入身份证号"
        :readonly="$route.query.status == 2?false:true"
        :rules="[{ required: true, message: '请输入身份证号' }]"
      />
      <!-- 公司证件信息 -->
      <div
        class="shenfen"
        v-if="doorType != '小微商户'"
      >
        <div class="shen_title">公司证件信息</div>
        <div
          class="shenfen_pic"
          style='justify-content:normal;padding:0 30px;'
        >
          <div>
            <van-uploader
              :max-count="1"
              :disabled='$route.query.id && $route.query.status == 2?true:false'
              :after-read="upLoadToQiniu_yingye"
            >
              <div v-if="(fileList.filter(item=>item.title == 'yingye')).length > 0">
                <img
                  v-for="(item,i) in fileList.filter(item=>item.title == 'yingye')"
                  :key="i"
                  :src="item.url"
                  style="width:108px;height:70px;"
                >
                <div class="re_push">重新上传</div>
              </div>
              <van-button
                v-else
                icon="add"
                type="primary"
              ></van-button>
            </van-uploader>
            <div class="p10">营业执照</div>
          </div>
        </div>
      </div>
      <!-- 结算银行信息 -->
      <div class="bankInfo">结算银行信息</div>
      <div
        v-if="!$route.query.id || $route.query.id && $route.query.status == '2'"
        class="door_type"
      >
        <div class="w_140">账户类型</div>
        <van-radio-group
          v-model="bankAccountType"
          direction="horizontal"
          style="display:flex;width:88%;"
        >
          <van-radio
            :name='item'
            v-for="(item,i) in doorType == '小微商户'?payeeAll.filter(item=>item == '对私转账'):payeeAll"
            :key="i"
          >{{ item }}</van-radio>
        </van-radio-group>
      </div>
      <div
        v-else
        class="scale_box"
      >
        <div class="w28">账户类型</div>
        <div>账户类型</div>
      </div>
      <van-field
        v-model="dataForm.accountBank"
        label="开户银行"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        :placeholder="bankAccountType == '对私转账'?'请输入银行名称，如工商银行':'需与营业执照上的商户名称一致'"
        :rules="[{ required: true, message: '请输入银行名称，如工商银行' }]"
      />
      <van-field
        v-model="dataForm.bankName"
        label="开户银行全称"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        placeholder="请输入开户银行全称，如工商银行合作支行"
        :rules="[{ required: true, message: '请输入开户银行全称' }]"
      />
      <van-field
        v-model="dataForm.accountNumber"
        label="银行账号"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        placeholder="请输入银行账号"
        :rules="[{ required: true, message: '请输入银行账号' }]"
      />
      <van-field
        v-model="dataForm.mobilePhone"
        label="管理员手机号"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        placeholder="请输入管理员手机号"
        :rules="[{ required: true, message: '请输入管理员手机号' }]"
      />
      <van-field
        v-if="doorType != '小微商户'"
        v-model="dataForm.contactEmail"
        label="超级管理员邮箱"
        :readonly="!$route.query.id || $route.query.id && $route.query.status == '2'?false:true"
        placeholder="请输入超级管理员邮箱"
        :rules="[{ required: true, message: '请输入超级管理员邮箱' }]"
      />
      <van-field
        v-if='dataForm.signUrl'
        v-model="dataForm.signUrl"
        label="签约网址"
        readonly
      >
        <template #button>
          <van-button size="small" type="primary" @click='gotoSign'>去签约</van-button>
        </template>
      </van-field>
      <van-field
        v-if='dataForm.remark'
        v-model="dataForm.remark"
        label="描述"
        readonly
      />
      <van-button
        v-if="!$route.query.id || $route.query.id && $route.query.status == '2'"
        class="submit-btn"
        block
        native-type="submit"
      >提交资料
      </van-button>
    </van-form>
    <van-popup
      v-model='user_show'
      position="bottom"
      style="width:100%;'"
    >
      <van-picker
        show-toolbar
        title="选择业主"
        @confirm="onConfirm"
        @cancel="user_show = false"
        :columns="users"
      />
    </van-popup>
  </div>
</template>
<script>
import { Toast } from 'vant';
import * as qiniu from 'qiniu-js';
import Exif from 'exif-js';
import { getLocalStorageFindHotel, getLocalStorageFindLeft } from '@/utils/dictType';
import { GetBusinessHotelMerchant, PostBusinessHotelMerchantAdd, PostBusinessHotelMerchantEdit } from '@/api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessQiniuAccesskey } from '@/api/api_hotel';
import { PostBusinessHotelLandlordList } from '@/api/api_business';
export default {
    data() {
        return {
            dataForm: {
                hotelId: this.$route.query.hotelId,
                merchantType: null,
                userId: null,
                organizationType: null,
                businessLicensePic: null,
                idCardPic: null,
                idCardNationalPic: null,
                bankAccountType: null,
                accountBank: null,
                bankName: null,
                accountNumber: null,
                mobilePhone: null,
                contactEmail: null,
                storeName: null
            },
            picForm: {
                picUrl: null,
                title: null
            },
            user_show: false,
            user: null,
            users: [],
            fileList: [],
            organizationTypes: [],
            payAll: ['酒店收款', '业主收款'],
            payeeAll: ['对私转账', '对公转账'],
            payType: '酒店收款',
            doorType: '小微商户',
            bankAccountType: '对私转账',
            status: null,
        };
    },
    watch: {
        'dataForm.userId': {
            handler(val) {
                if ((val && this.payType == '业主收款') || (val && this.dataForm.merchantType == '2')) {
                    this.dataForm.mobilePhone = this.userAll.find((item) => item.userId == val).sysUser.phone;
                }
            }
        }
    },
    created() {
      console.log('当前环境:',window.navigator.userAgent.toLowerCase())
        this.$store.commit('setHeader', '提交商户信息');
        this.status = this.$route.query.status;
        this.dataForm.storeName = getLocalStorageFindHotel('c_hotelAll', this.$route.query.hotelId);
        this.getType();
    },
    methods: {
      gotoSign() {
        const url  = this.dataForm.signUrl;
        const ua = navigator.userAgent.toLowerCase();
        if (
          !(ua.match(/MicroMessenger/i) == "micromessenger")
        ) {
          window.open(`${url}`, '_blank');
        } else {
          window.open(`${url}`, '_blank');
        }
      },
        getType() {
            PostBusinessHotelLandlordList(this.$route.query.hotelId).done((res) => {
                if (res.data.length > '0') {
                    this.userAll = res.data;
                    this.users = res.data.map((item) => item.sysUser.realName);
                    this.dataForm.userId = this.userAll[0].userId;
                    if (this.dataForm.merchantType == '2') {
                        this.dataForm.mobilePhone = this.userAll.find((item) => item.userId == this.dataForm.userId).sysUser.phone;
                    }
                    if (this.$route.query.id && this.payType == '业主收款') {
                        this.user = this.userAll[0].sysUser.realName;
                    } else {
                        this.user = this.userAll[0].sysUser.realName + ' ' + '>';
                    }
                }
                GetSystemDictDataByType({ dictType: 'business_wx_organization_type' }).done((res) => {
                    if (res.data.length > '0') {
                        this.organizationAllTypes = res.data;
                        this.organizationTypes = res.data.map((item) => item.dictLabel);
                        if (this.dataForm.id) {
                            this.doorType = this.organizationAllTypes.find((item) => item.dictValue == this.dataForm.organizationType).dictLabel;
                        } else {
                            this.doorType = this.organizationAllTypes[0].dictLabel;
                            this.dataForm.organizationType = this.organizationAllTypes[0].dictValue;
                        }
                    }
                    this.getDetailInfo();
                });
            });
        },
        getDetailInfo() {
            if (this.$route.query.id) {
                GetBusinessHotelMerchant(this.$route.query.id, this.$route.query.hotelId).done((res) => {
                    this.dataForm = { ...this.dataForm, ...res.data };
                    if (this.dataForm.idCardPic) {
                        let obj = {};
                        obj.title = 'front';
                        const turl = this.dataForm.domainName + this.dataForm.idCardPic;
                        obj.url = turl;
                        console.log('省份正人面照片:',turl);
                        this.fileList.push(obj);
                    }
                    if (this.dataForm.idCardNationalPic) {
                        let obj = {};
                        obj.title = 'back';
                        const tttutl = `${this.dataForm.domainName}${this.dataForm.idCardNationalPic}`
                        // obj.url = this.dataForm.domainName + this.dataForm.idCardNationalPic;
                        console.log('省份证国旗照片:',tttutl);
                        this.fileList.push(obj);
                    }
                    if (this.dataForm.businessLicensePic) {
                        let obj = {};
                        obj.title = 'yingye';
                        obj.url = this.dataForm.domainName + this.dataForm.businessLicensePic;
                        this.fileList.push(obj);
                    }
                    this.payType = this.dataForm.merchantType == '1' ? this.payAll[0] : this.payAll[1];
                    this.bankAccountType = this.dataForm.bankAccountType == '74' ? this.payeeAll[1] : this.payeeAll[0];
                });
            } else {
                this.dataForm.mobilePhone = getLocalStorageFindLeft('current_user').phone;
            }
            this.getqiniu();
        },
        // 七牛云
        getqiniu() {
            GetBusinessQiniuAccesskey().done((res) => {
                this.token = res.data;
            });
        },
        // 身份证正面
        async upLoadToQiniu_front(req) {
            this.picForm.title = 'front';
            this.addTitle(req);
        },
        // 身份证背面
        async upLoadToQiniu_back(req) {
            this.picForm.title = 'back';
            this.addTitle(req);
        },
        // 营业执照
        async upLoadToQiniu_yingye(req) {
            this.picForm.title = 'yingye';
            this.addTitle(req);
        },
        changePayType(type) {
            if (type == '酒店收款') {
                this.dataForm.mobilePhone = getLocalStorageFindLeft('current_user').phone;
            } else {
                this.dataForm.userId = this.userAll[0].userId;
                this.user = this.userAll[0].sysUser.realName + ' ' + '>';
                this.dataForm.mobilePhone = this.userAll[0].sysUser.phone;
            }
        },
        changeDoorType(type) {
            if (type == '小微商户') {
                this.bankAccountType = '对私转账';
            }
        },
        onConfirm(e) {
            this.user = e + ' ' + '>';
            this.dataForm.userId = this.userAll.find((item) => item.sysUser.realName == e).userId;
            this.dataForm.mobilePhone = this.userAll.find((item) => item.sysUser.realName == e).sysUser.phone;
            this.user_show = false;
        },
        // 组件方法 获取 流
        addTitle(file) {
            this.filesName = file.file.name; // 获取文件名
            this.filesType = file.file.type; // 获取类型
            this.imgPreview(file.file);
        },
        // 处理图片
        imgPreview(file) {
            let self = this;
            let Orientation;
            //去获取拍照时的信息，解决拍出来的照片旋转问题   npm install exif-js --save   这里需要安装一下包
            Exif.getData(file, function () {
                Orientation = Exif.getTag(this, 'Orientation');
            });
            // 看支持不支持FileReader
            if (!file || !window.FileReader) return;
            if (/^image/.test(file.type)) {
                // 创建一个reader
                let reader = new FileReader();
                // 将图片2将转成 base64 格式
                reader.readAsDataURL(file);
                // 读取成功后的回调
                reader.onloadend = function () {
                    let result = this.result;
                    let img = new Image();
                    img.src = result;
                    if (this.result.length <= 500 * 1024) {
                        // 判断图片是否大于500K,是就直接上传，反之压缩图片
                        // 上传图片
                        self.postImg(this.result);
                    } else {
                        img.onload = function () {
                            let data = self.compress(img, Orientation);
                            // 上传图片
                            self.postImg(data);
                        };
                    }
                };
            }
        },
        // 压缩图片
        compress(img, Orientation) {
            let canvas = document.createElement('canvas');
            let ctx = canvas.getContext('2d');
            let tCanvas = document.createElement('canvas'); // 瓦片canvas
            let tctx = tCanvas.getContext('2d');
            let width = img.width;
            let height = img.height;
            let ratio; // 如果图片大于四百万像素，计算压缩比并将大小压至400万以下
            if ((ratio = (width * height) / 4000000) > 1) {
                ratio = Math.sqrt(ratio);
                width /= ratio;
                height /= ratio;
            } else {
                ratio = 1;
            }
            canvas.width = width;
            canvas.height = height;
            ctx.fillStyle = '#fff'; // 铺底色
            ctx.fillRect(0, 0, canvas.width, canvas.height); // 如果图片像素大于100万则使用瓦片绘制
            let count;
            if ((count = (width * height) / 1000000) > 1) {
                count = ~~(Math.sqrt(count) + 1); //计算要分成多少块瓦片
                let nw = ~~(width / count); // 计算每块瓦片的宽和高
                let nh = ~~(height / count);
                tCanvas.width = nw;
                tCanvas.height = nh;
                for (let i = 0; i < count; i++) {
                    for (let j = 0; j < count; j++) {
                        tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
                        ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
                    }
                }
            } else {
                ctx.drawImage(img, 0, 0, width, height);
            }
            //进行最小压缩
            let ndata = canvas.toDataURL('image/jpeg', 0.1);
            tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
            return ndata;
        },
        //将base64转换为文件
        dataURLtoFile(dataurl) {
            var arr = dataurl.split(','),
                bstr = atob(arr[1]),
                n = bstr.length,
                u8arr = new Uint8Array(n);
            while (n--) {
                u8arr[n] = bstr.charCodeAt(n);
            }
            return new File([u8arr], this.filesName, {
                type: this.filesType
            });
        },
        // 提交图片到后端
        postImg(base64) {
            let file = this.dataURLtoFile(base64);
            let formData = new window.FormData();
            formData.append('file', file);
            this._addTitle(file);
        },
        _addTitle(req) {
            const config = {
                headers: { 'Content-Type': 'multipart/form-data' },
                domain: ''
            };
            let fileType = '';
            if (req.type ==
 'image/png') {
                fileType = 'png';
            } else {
                fileType = 'jpg';
            }
            // 上传时候的Token,可前端自己生成,安全起见后端生成！
            const token = this.token;
            const formdata = new FormData();
            formdata.append('file', req);
            formdata.append('token', token);
            let _this = this;
            const observer = {
                next(res) {},
                error(err) {},
                complete(res) {
                    _this.picForm.picUrl = res.key;
                    _this.upLoadPic();
                }
            };
            let _api = this.picForm.title ==
 'yingye' ? 'hotel/merchant/business/license/' : 'hotel/merchant/card/';
            const observable = qiniu.upload(req, _api + new Date().getTime() + '.png', token, config);
            observable.subscribe(observer); // 上传开始
        },
        // 上传图片
        upLoadPic() {
            this.fileList.map((item, i) => {
                if (item.title == this.picForm.title) {
                    this.fileList.splice(i, 1);
                }
            });
            // 正式：http://qiniu.aiyoyoufang.com/   29
            // 测试：http://qiniutest.aiyoyoufang.com/   32
            let obj = {
                title: this.picForm.title,
                url: 'http://qiniu.aiyoyoufang.com/' + this.picForm.picUrl
            };
            this.fileList.push(obj);
        },
        // 提交
        onSubmit() {
            this.dataForm.merchantType = this.payType == '酒店收款' ? '1' : '2';
            this.dataForm.bankAccountType = this.bankAccountType == '对私转账' ? '75' : '74';
            this.dataForm.organizationType = this.organizationAllTypes.find((item) => item.dictLabel == this.doorType).dictValue;
            if (this.payType == '酒店收款') {
                delete this.dataForm.userId;
            }
            this.fileList.map((item) => {
                if (item.title == 'front') {
                    this.dataForm.idCardPic = item.url.substring(29);
                }
                if (item.title == 'back') {
                    this.dataForm.idCardNationalPic = item.url.substring(29);
                }
                if (item.title == 'yingye') {
                    this.dataForm.businessLicensePic = item.url.substring(29);
                }
            });
            if (this.doorType == '小微商户') {
                delete this.dataForm.businessLicensePic;
                delete this.dataForm.contactEmail;
            }
            let editData = {
                id: this.$route.query.id,
                organizationType: this.dataForm.organizationType,
                businessLicensePic: this.dataForm.businessLicensePic,
                idCardPic: this.dataForm.idCardPic,
                idCardNationalPic: this.dataForm.idCardNationalPic,
                bankAccountType: this.dataForm.bankAccountType,
                accountBank: this.dataForm.accountBank,
                bankName: this.dataForm.bankName,
                accountNumber: this.dataForm.accountNumber,
                storeName: this.dataForm.storeName,
                mobilePhone: this.dataForm.mobilePhone,
                contactEmail: this.dataForm.contactEmail
            };
            let _api = this.$route.query.id ? PostBusinessHotelMerchantEdit : PostBusinessHotelMerchantAdd;
            let _data = this.$route.query.id ? editData : this.dataForm;
            // _data = {
            //     ..._data,
            //     accountBank: "其他银行",
            // };
            _api(_data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-1)', 900);
            });
        }
    }
};
</script>
<style scoped>
