    text-align: left;
    padding: 0 16px;
}
.shen_title {
    font-size: 15px;
    font-weight: bold;
    padding: 10px 0;
}
.shenfen_pic {
    display: flex;
    justify-content: space-around;
    padding: 4px 0 10px;
}
.scale_box {
    display: flex;
    padding: 10px 16px;
