}
/deep/ .van-radio--horizontal {
    margin-right: 0;
    width: 50%;
    margin-bottom: 8px;
}
/deep/ .van-field__label {
    color: #000;
}
/deep/ .van-button--normal {
    width: 108px;
    height: 70px;
    padding: 0;
    background: #fff6f1;
    border: none;
}
/deep/ .van-button__icon {
    font-size: 24px;
}
/deep/ .van-button--primary {
    color: #ff7d41;
}
/deep/ .van-radio__icon--checked .van-icon {
    color: #fff;
    background: #ff7d41;
    border-color: #ff7d41;
}
.submit-btn {
    background: #ff7d41;
    color: #fff;
    width: 100%;
    height: 40px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
</style>



